@import '../../globals.scss';

.home-root {
  margin-top: $tablet-gutter + 80px;
  margin-bottom: $tablet-gutter;
  margin-left: $tablet-gutter;
  margin-right: $tablet-gutter;
}

.home-tile {
  display: flex;
  margin-top: $tablet-gutter;
}

.landing-page-card {
  height: calc((100vh - 300px) / 3);
  width: 50%;
  min-height: 200px;
}

.landing-paragraph {
  width: 50%;

  p {
    padding-left: $mobile-gutter;
    padding-top: $mobile-gutter;
    padding-bottom: $mobile-gutter;
  }
}

.align-right {
  a {
    position: relative;
    left: 50%;
  }
  .landing-paragraph {
    position: relative;
    right: 50%;

    p {
      padding-left: 0;
      padding-right: $mobile-gutter;
    }
  }
}



